import React, { Component } from 'react';
import { Select } from 'antd';

const { Option } = Select;
export default class SelectGender extends Component {
  state = {};
  render() {
    return (
      <Select {...this.props}>
        <Option value>Nam</Option>
        <Option value={false}>Nữ</Option>
      </Select>
    );
  }
}



