import React, { Component, Fragment } from 'react';
import { BtnEdit, BtnDelete } from 'components/common/button';
import { Popconfirm } from 'antd';
import { Link } from 'react-router-dom';

export default class ColEditDelete extends Component {
  sate = {};
  render() {
    const { props } = this;
    return (
      <Fragment>
        {!props.hidenBtnEdit && props.hrefEdit && <BtnEdit href={props.hrefEdit} />}
        {!props.hidenBtnEdit && props.linkEdit && (
          <Link {...props.linkEdit}>
            <BtnEdit />
          </Link>
        )}

        {!props.hidenBtnDelete ? (
          <Popconfirm
            title="Bạn chắc chắn muốn xóa?"
            onConfirm={props.onConfirmDelete}
            okText="Xóa"
            cancelText="Hủy"
          >
            <BtnDelete
              onClick={e => {
                e.stopPropagation();
              }}
            />
          </Popconfirm>
        ) : null}
      </Fragment>
    );
  }
}



