import { message } from 'antd';

export const setFieldsValueCustom = (form, data) => {
  Object.keys(form.getFieldsValue()).forEach(key => {
    const obj = {};
    obj[key] = data[key] || null;
    if (obj[key]) form.setFieldsValue(obj);
  });
}

export const getFormTableData = (values, rowKey) => {
  const tempObj = {};
  Object.keys(values).forEach((value) => {
    const keyOfRow = value.split('-')[0];
    const id = value.split('-')[1];
    if (id) {
      if (!tempObj[id]) {
        tempObj[id] = {};
        if (rowKey) {
          tempObj[id].id = parseInt(id.split('_')[1]);
          tempObj[id][rowKey] = id;
        }
      }
      tempObj[id][keyOfRow] = values[value];
    }
  });
  return tempObj;
}

export const convertTableDataObjToArray = (obj) => {
  const newArray = [];
  Object.keys(obj).forEach((value) => {
    newArray.push(obj[value]);
  });
  return newArray;
}

export const mergeArrayAtoB = (arrayHaveNewData, arrayNeedMerge, key) => {
  const tempNeedMerge = [...arrayNeedMerge];
  arrayHaveNewData.forEach(itemNew => {
    const itemNeedMergeIndex = tempNeedMerge.findIndex(item => `${item[key]}` === `${itemNew[key]}`);
    if (itemNeedMergeIndex !== -1) {
      tempNeedMerge[itemNeedMergeIndex] = Object.assign({}, arrayNeedMerge[itemNeedMergeIndex], itemNew);
    }
  });
  return tempNeedMerge;
}

export const getListFieldsNeedValidate = (data, rowKey, listFieldName) => {
  const listFieldsNeedValidate = [];
  data.forEach(item => {
    listFieldName.forEach(fieldName => listFieldsNeedValidate.push(`${fieldName}-${item[rowKey]}`));
  });
  return listFieldsNeedValidate;
}

export const areFieldsValidate = (data, listFieldName) => {
  let isValidate = true;
  data.forEach(item => {
    listFieldName.forEach(fieldName => {
      if (item[fieldName] === null || item[fieldName] === undefined || item[fieldName] === '') {
        isValidate = false;
      }
    });
  });
  return isValidate;
}

export function getListDataValid(rowKey, listFieldName, refForm, refFormRaw, selectedRows) {
  return new Promise((resolve, reject) => {
    try {
      const currentTableData = refFormRaw.getTableData();
      let listSelectedItem = [];
      if (selectedRows && selectedRows.length) {
        const listSelectedKey = selectedRows.map(item => {
          return item[rowKey];
        });
        listSelectedItem = currentTableData.filter(item => listSelectedKey.includes(item[rowKey]));
      } else {
        listSelectedItem = [...currentTableData];
      }
      const listFieldsNeedValidate = getListFieldsNeedValidate(listSelectedItem, rowKey, listFieldName);
      return refForm.current.validateFields(listFieldsNeedValidate, (err2) => {
        if (err2) {
          reject();
          return;
        }
        if (areFieldsValidate(listSelectedItem, listFieldName) === false) {
          message.warning('Có dữ liệu ở trang khác chưa được điền đủ!');
          reject();
          return;
        }
        resolve(listSelectedItem);
        // return listSelectedItem;
      });
    } catch (error) {
      console.log(error);
      reject();
      return [];
    }
  });

}


