import React, { Component } from 'react';
import { DatePicker } from 'antd';
import { dateToStringParamReq, stringParamReqToDate, DATE_SHOW_UI } from 'utils/formatUtil';

const { RangePicker } = DatePicker;

export default class DateRangePicker extends Component {
  state = {
    valuesDate: [],
    fromValueStr: '',
    toValueStr: '',
  };

  componentWillMount() {
    // props.value string
    this.stringToDate(this.props.value);
  }

  componentWillReceiveProps = nextProps => {
    const { fromField, toField } = this.props;
    const { getFieldValue } = this.props.form;
    const fromValueStr = getFieldValue(fromField);
    const toValueStr = getFieldValue(toField);
    if (nextProps.value !== this.props.value) {
      console.log('fromValueStr', fromValueStr);
      this.stringToDate(nextProps.value);
    } else if (
      fromValueStr &&
      toValueStr &&
      fromValueStr !== this.state.fromValueStr &&
      toValueStr !== this.state.toValueStr
    ) {
      console.log('toValueStr', toValueStr);
      this.stringToDate([fromValueStr, toValueStr]);
    }
  };

  stringToDate = valuesStr => {
    if (!valuesStr || valuesStr.length === 0) {
      this.setState({ valuesDate: [], fromValueStr: '', toValueStr: '' });
      return;
    }
    let valuesStrArray = valuesStr;
    if (valuesStrArray.length === 21) {
      valuesStrArray = valuesStrArray.split(',');
    }
    this.setState({
      valuesDate: [
        stringParamReqToDate(valuesStrArray[0]),
        stringParamReqToDate(valuesStrArray[1]),
      ],
      fromValueStr: valuesStrArray[0],
      toValueStr: valuesStrArray[1],
    });
  };

  handleChange = valuesDate => {
    const { fromField, toField } = this.props;
    const { setFieldsValue } = this.props.form;
    let fromValueStr = '';
    let toValueStr = '';
    let valuesStr = [];
    if (valuesDate && valuesDate.length === 2) {
      fromValueStr = dateToStringParamReq(valuesDate[0]);
      toValueStr = dateToStringParamReq(valuesDate[1]);
      valuesStr = [fromValueStr, toValueStr];
    }
    console.log(fromValueStr, toValueStr);
    setFieldsValue({
      [fromField]: fromValueStr,
      [toField]: toValueStr,
    });
    this.setState({ fromValueStr, toValueStr });
    this.props.onChange(valuesStr);
  };

  createField = () => {
    const { fromField, toField } = this.props;
    const { getFieldDecorator } = this.props.form;
    getFieldDecorator(fromField);
    getFieldDecorator(toField);
  };

  render() {
    const { valuesDate } = this.state;
    this.createField();
    return (
      <RangePicker
        format={DATE_SHOW_UI}
        {...this.props}
        value={valuesDate}
        onChange={this.handleChange}
      />
    );
  }
}



