import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getTuitionReceipts = ({
  studentId,
  candidateId,
  trainTypeID,
  studyFieldID,
  yearEduId,
}) =>
  request({
    prefix,
    url: '/EnrollReceivable/GetEnrollReceivableByStudent',
    method: 'GET',
    params: {
      studentId,
      candidateId,
      trainTypeID,
      studyFieldID,
      yearEduId,
      isPagingEnabled: false,
    },
    data: {},
  });

export const getStudents = ({
  trainTypeId,
  studyFieldId,
  candidateCode,
  fullName,
  studentCode,
  dOB,
  isApply = false,
  isHaveClass,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix: '/api/students',
    url: '/Student/GetEnrollStudents',
    method: 'GET',
    params: {
      trainTypeId,
      studyFieldId,
      candidateCode,
      fullName,
      studentCode,
      dOB,
      isApply,
      isHaveClass,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getReceiptConfig = () =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: { configTypeId: 0 },
    data: {},
  });

export const saveReceipt = async res =>
  request({
    prefix,
    url: '/Receipt/CreateEnrollReceipt',
    method: 'POST',
    params: {},
    data: { ...res },
  });

export const exportReceipt = async (id, name) =>
  requestDownload({
    defaultFileName: `${name}.pdf`,
    prefix,
    url: `/Receipt/ExportEnrollReceipt?receiptId=${id}`,
    data: {},
    method: 'POST',
  });



