import React, { Component } from 'react';
import {
  Table
} from 'antd';

import {
  getFormTableData,
  convertTableDataObjToArray,
  mergeArrayAtoB
} from 'utils/formUtil';

class TableForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataToSave: [...this.props.data],
      dataToView: [...this.props.data],
      filterParams: { ...this.props.filterParams },
    };
  }

  componentWillReceiveProps = nextProps => {
    const { filterParams } = this.state;
    if (nextProps.data !== this.props.data) {
      this.setState({
        dataToSave: [...nextProps.data],
      },
        () => {
          this.filterItemsByParams(filterParams)
        }
      );
    }
    if (nextProps.filterParams !== this.props.filterParams) {
      const newData = this.getTableData();
      this.setState({
        dataToSave: [...newData],
        filterParams: {...nextProps.filterParams},
      },
        () => this.filterItemsByParams(nextProps.filterParams)
      );
    }
  };

  getCurrentPageData = (values) => {
    const { rowKey } = this.props;
    const formDataObj = getFormTableData(values, rowKey);
    const formDataArray = convertTableDataObjToArray(formDataObj);
    return formDataArray;
  }

  getTableData = () => {
    const { dataToSave } = this.state;
    const { rowKey } = this.props;
    const currentValuesPage = this.props.form.getFieldsValue();
    const formatData = this.getCurrentPageData(currentValuesPage);
    const newData = mergeArrayAtoB(formatData, dataToSave, rowKey);
    return newData;
  }

  filterItemsByParams = (filterParams) => {
    const { dataToSave } = this.state;
    let tempDataToView = [...dataToSave];
    Object.keys(filterParams).forEach(key => {
      if (filterParams[key]) {
        tempDataToView = tempDataToView.filter(item => `${item[key]}` === `${filterParams[key]}`);
      }
    });
    this.setState({
      dataToView: [...tempDataToView],
    });
  }

  render() {
    const { dataToView, filterParams } = this.state;
    const { reload, rowKey, columns } = this.props;
    const customConditionsProps = {};
    if (this.props.handleSelectedRows) {
      customConditionsProps.rowSelection = {
        onChange: (_selectedRowKeys, selectedRows) => {
          this.props.handleSelectedRows(selectedRows);
        },
      };
    }
    return (
      <Table
        reload={reload}
        columns={columns}
        dataSource={dataToView}
        scroll={{ x: true }}
        rowKey={rowKey}
        onChange={() => {
          const newData = this.getTableData();
          this.setState({
            dataToSave: [...newData],
          },
            () => {
              this.filterItemsByParams(filterParams)
            }
          );
        }}
        // pagination={{ pageSize: 5 }}
        {...customConditionsProps}
      />
    );
  }
}

export default TableForm;



