import React, { Component } from 'react';
import { Select } from 'antd';
// import { objectEquals } from 'utils/functionUtil';

const { Option } = Select;
export default class SelectOfTable extends Component {
  static defaultProps = {
    keyValue: 'id',
    keyLabel: 'label',
    allowClear: true,
    allowGetData: true,
    allowGetObjSelected: false,
    typeValue: 'string',
    disabled: false,
  };

  render() {
    const { data, loading, onChange, keyValue, keyLabel, disabled } = this.props;
    return (
      <Select
        showSearch
        style={{ width: '100%' }}
        loading={loading}
        filterOption={(input, option) =>
          option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
        {...this.props}
        onChange={onChange}
        disabled={disabled}
      >
        {data.map(item => (
          <Option key={item[keyValue]} value={item[keyValue]}>
            {item[keyLabel]}
          </Option>
        ))}
      </Select>
    );
  }
}



