import React, { Component } from 'react';
import { Row, Col, Form, Input, Icon } from 'antd';
import moment from 'moment';
import { SelectAjax, DatePickerString } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { Btn } from 'components/common/button';
import InputMoney from 'components/common/input/InputMoney';
import { getSemesters } from 'services/semesterService';
import { getYearEducations } from 'services/yearEducationService';
import { getReceivable } from 'services/receivable';
import { getReceiptConfig } from 'services/tuitionReceipts';
import { moneyToString } from 'utils/formatUtil';
import { getUserInfo } from 'utils/userUtils';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = { receivable: {}, receiptConfig: {} };

  componentDidMount() {
    this.getReceiptConfig();
  }

  getReceiptConfig = async () => {
    const res = await getReceiptConfig();
    this.setState({ receiptConfig: res.data.items[0] });
  };

  getReceivable = async () => {
    const res = await getReceivable();
    const receivable = res.data.items.reduce((result, obj) => {
      const key = obj.id;
      result[key] = obj.label;
      return result;
    }, {});
    this.setState({ receivable });
    return res;
  };

  getCurrencyType = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'VNĐ',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getPaymentTypeId = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Trực tiếp',
        },
        {
          id: 'hi',
          label: 'Chuyển khoản',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 2,
    };
    const res = {
      data,
    };
    return res;
  };

  getPaymentTime = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
        {
          id: '3',
          label: '3',
        },
        {
          id: '4',
          label: '4',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 4,
    };
    const res = {
      data,
    };
    return res;
  };

  setVisibleModal = () => {
    const { setVisibleModal } = this.props;
    setVisibleModal(true);
  };

  formItemLayout = 6;

  handleAddReceipt = async () => {
    const { getFieldValue, resetFields } = this.props.form;
    const { receivable } = this.state;
    const amount = getFieldValue('amount');
    const receivableId = getFieldValue('receivable');

    if (!receivableId || !amount) return;
    const receivableLabel = receivable[receivableId];
    this.props.handleAddOther(receivableId, receivableLabel, amount);
    resetFields(['receivable', 'amount']);
  };

  handleRemoveReceipt = () => {
    this.props.handleRemoveReceipt();
  };

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const { selectedStudent, receiptSelecteds, handleResetWhenChange } = this.props;
    const { receiptConfig } = this.state;
    const totalAmount = receiptSelecteds
      .map(receiptSelected => receiptSelected.paymentAmount)
      .reduce((a, b) => a + b, 0);

    const newDate = new Date();
    const date = newDate.getDate();
    const month = newDate.getMonth() + 1;
    const year = newDate.getFullYear();
    const separator = '/';
    const currentDate = `${date}${separator}${
      month < 10 ? `0${month}` : `${month}`
    }${separator}${year}`;

    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const paymentTypeId = getFieldValue('paymentTypeId');
    const paymentTime = getFieldValue('paymentTime');
    const currencyType = getFieldValue('currencyType');
    const casherId = getUserInfo().username;
    const enableSearch =
      !!yearEduId && !!semesterId && !!paymentTypeId && !!paymentTime && !!currencyType;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical">
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                <SelectAjax
                  service={getYearEducations}
                  setFieldsValue={setFieldsValue}
                  onChange={handleResetWhenChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  onChange={handleResetWhenChange}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lần thu">
              {getFieldDecorator('paymentTime', { initialValue: '1', rules: [requiredRule] })(
                <SelectAjax service={this.getPaymentTime} />
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Hình thức thu">
              {getFieldDecorator('paymentTypeId', { initialValue: '1', rules: [requiredRule] })(
                <SelectAjax service={this.getPaymentTypeId} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={0}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('receiptConfigId', {
                initialValue: receiptConfig.id,
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã hóa đơn">
              {getFieldDecorator('invoiceCode', {
                initialValue: receiptConfig.invoiceCode,
                rules: [requiredRule],
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Loại tiền">
              {getFieldDecorator('currencyType', { initialValue: '1', rules: [requiredRule] })(
                <SelectAjax service={this.getCurrencyType} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Ngày thu">
              {getFieldDecorator('paymentDate', {
                initialValue: moment(currentDate, 'DD-MM-YYYY'),
                rules: [requiredRule],
              })(<DatePickerString placeholder="Ngày thu" />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item {...formItemLayout} label="Người thu">
              {getFieldDecorator('casher', {
                initialValue: casherId,
                rules: [requiredRule],
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã SV / SBD">
              {getFieldDecorator('studentCode', {
                initialValue: selectedStudent.studentCode,
                rules: [requiredRule],
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
          <Col span={2}>
            <Btn onClick={this.setVisibleModal} disabled={!enableSearch}>
              <Icon type="search" />
            </Btn>
          </Col>
          <Col span={6}>
            <span style={{ fontWeight: 'bold' }}>Họ và tên:</span> {selectedStudent.fullname}
            <br />
            <span style={{ fontWeight: 'bold' }}>Lớp:</span> {selectedStudent.classLabel}
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Form.Item {...formItemLayout} label="Nội dung">
              {getFieldDecorator('paymentContent', { rules: [requiredRule] })(
                <Input.TextArea style={{ height: '75px' }} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Thu khác">
              {getFieldDecorator('receivable', {})(
                <SelectAjax service={this.getReceivable} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số tiền">
              {getFieldDecorator('amount', {})(<InputMoney style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Btn
              style={{ marginLeft: '10px' }}
              onClick={this.handleAddReceipt}
              disabled={!selectedStudent.studentCode}
            >
              Thêm <Icon type="plus" />
            </Btn>
            <Btn style={{ marginLeft: '10px' }} onClick={this.handleRemoveReceipt}>
              Xóa <Icon type="delete" />
            </Btn>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <b style={{ marginRight: '20px' }}>Tổng số tiền:</b>
            <b style={{ color: 'red', fontSize: '15px' }}>{moneyToString(totalAmount)} VNĐ</b>
          </Col>
          {/* <Col span={12}>
            <span style={{ color: 'red', fontSize: '15px' }}>
              (Ba triệu sáu trăm bảy mươi ngàn đồng)
            </span>
          </Col> */}
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



