import React, { Component } from 'react';
import { Table } from 'antd';
import { makeid } from 'utils/functionUtil';
import { stringify } from 'querystring';

export default class TableData extends Component {
  static defaultProps = {
    allowGetData: true,
  };

  state = {
    data: [],
    loading: false,
    pagination: {
      pageSize: 10,
    },
    // page: 0,
  };

  componentDidMount() {
    const { paramSearch, allowGetData } = this.props;
    if (paramSearch) {
      if (allowGetData) {
        this.actionGetData(this.props);
      }
    } else {
      this.actionGetData(this.props);
    }
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.clear !== 0 && nextProps.clear !== this.props.clear) {
      this.setState({
        data: [],
        loading: false,
        pagination: {
          pageSize: 10,
        },
      });
      return;
    }
    if (nextProps.reload !== this.props.reload) {
      this.actionGetData({ ...nextProps });
      this.setState({ pagination: { ...this.state.pagination, ...{ current: 1 } } });
    }
    if (stringify(nextProps.paramSearch) !== stringify(this.props.paramSearch)) {
      this.actionGetData({ ...nextProps });
    }
  };

  handleTableChange = pagination => {
    this.actionGetData(this.props, { page: pagination.current });
  };

  actionGetData = async (props, { page } = { pageSize: 10 }) => {
    const { allowGetData, paramSearch, service } = props;

    if (this.state.loading || !allowGetData) {
      return;
    }

    this.setState({
      loading: true,
    });

    try {
      const res = await service({ page, ...paramSearch });
      const pagination = { ...this.state.pagination };
      pagination.total = res.data.totalResults;
      pagination.current = page;
      if (this.props.dataTable) {
        this.props.dataTable(res.data.items);
      }
      const { rowKey } = this.props;
      let tempData = [...res.data.items];
      if (res.data.items && res.data.items.length && res.data.items[0][rowKey] === undefined) {
        tempData = tempData.map(item => {
          item[rowKey] = makeid(8);
          return item;
        });
      }
      this.setState({
        data: [...tempData],
        loading: false,
        pagination,
      });
    } catch (error) {
      this.setState({
        loading: false,
      });
    }
  };

  render() {
    return (
      <Table
        bordered
        scroll={{ x: true }}
        loading={this.state.loading}
        pagination={this.state.pagination}
        onChange={this.handleTableChange}
        dataSource={this.state.data}
        columns={this.props.columns}
        {...this.props}
      />
    );
  }
}



