import React, { Component } from 'react';
import { Row, Col, Form, Checkbox, Input, message, Radio } from 'antd';
import { DatePickerString, SelectAjax } from 'components/common';
import { Btn, BtnClear } from 'components/common/button';
import { getTrainTypes } from 'services/trainType';
import { getStudyFields } from 'services/StudyField';

@Form.create({ name: 'SearchBarStudent' })
class SearchBarStudent extends Component {
  state = {};

  exportExcel = () => {
    message.success('Demo disable when table data is empty');
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      values.isHaveClass = values.isHaveClass === 'true';
      this.props.handleParamSearchStudent(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hệ">
              {getFieldDecorator('trainTypeId', {})(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator('studyFieldId', {})(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  allowGetData
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số BD">
              {getFieldDecorator('candidateCode', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Họ và tên">
              {getFieldDecorator('fullName', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã SV">
              {getFieldDecorator('studentCode', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Ngày sinh">
              {getFieldDecorator('dOB', {})(<DatePickerString placeholder="Ngày sinh" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('isApply', { valuePropName: 'checked' })(
                <Checkbox>Chỉ tìm những sv đã nộp hồ sơ, giấy tờ bên QLSV</Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('isHaveClass')(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value="true">Đã phân lớp</Radio>
                  <Radio value="false">Chưa phân lớp</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <Btn htmlType="submit" title="Tìm kiếm SV" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarStudent;



