import React, { Component } from 'react';
import { Switch } from 'antd';

export default class Switch2Value extends Component {
  static defaultProps = {
    values: [false, true],
  };

  componentWillReceiveProps = (nextProps) => {
    if (!nextProps.values.includes(nextProps.value)) {
      this.onChange(false);
    }
  };

  onChange = checked => {
    const valueIndex = checked ? 1 : 0;
    const value = this.props.values[valueIndex];
    this.props.onChange(value);
  };

  render() {
    return <Switch {...this.props} onChange={this.onChange} />;
  }
}



